.wrapper{
  width: 420px;
  background: transparent;
  border: 2px solid rgba(225,225,225,.2);
  backdrop-filter: blur(30px);
  box-shadow: rgba(0,0,0,.2);
  color: #fff;
  border-radius: 10px;
  padding: 30px 40px;

}
.wrapper h1 {
  font-size: 36px;
  text-align: center;
}
.wrapper .input-box {
  position: relative;
  width: 90%;
  height: 70px;
 
  margin: 30px 0;

}
.input-box input {
  width: 100%;
  height: 100%;
  background-color: rgba(225,225,225,.2);
  border: none;
  outline: none;
  border: 20px solid rgba(225,225,225,.2);
  border-radius: 40px;
  font-size: 16px;
  padding: 20px 45px 20px 20px;
} 
.input-box input ::placeholder {
  color: #fff;

}
.input-box .icon {
  position: absolute;
  right: 40px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 16px;
}
.wrapper .remember-Forgot {
  display: flex;
  justify-content: space-between;
  font-size: 14.5px;
  margin: -15px 0 15px;

}
.remember-Forgot .lable input {
  accent-color: #fff;
  margin-right: 4px;

}
.remember-Forgot a {
  color: #fff;
  text-decoration: none;
}
.remember-Forgot a :hover {
  text-decoration: underline;

}
.wrapper button {
  width: 100%;
  height: 45px;
  background-color: #fff;
  border: none;
  outline: none;
  border-radius: 40px;
  box-shadow: 0 0 10px rgba(0,0,0,.1);
  cursor: pointer;
  font-size: 16px;
  color: #333;
  font-weight: 700;

}
.wrapper .register-link {
  font-size: 14.5px;
  text-align: center;
  margin: 20px 0 15px;
  
}
.register-link p a {
  color: #fff;
  text-decoration: none;
  font-weight: 600;

}
.register-link p a :hover {
  text-decoration: underline;
}
